<template>
	<div>
		<Loader v-if="isLoading" />
		<v-data-table
			v-else
			class="mt-6"
			:headers="header"
			:items="dataJurusan"
			:items-per-page="10">
			<template v-slot:no-data>
	      <v-alert type="info" class="mt-4">
	        <div>Data tidak ditemukan</div>
	      </v-alert>
	    </template>
	    <template v-slot:item.no="{item}">
	    	{{dataJurusan.map(x => x.id).indexOf(item.id)+1}}    	
	    </template>
			<template v-slot:item.action="{item}">
				<v-btn icon color="success">
					<v-icon small>mdi-eye</v-icon>
				</v-btn>
		    <EditJurusan :data="item" />				
				<HapusJurusan :id="item.id" />
			</template>		
		</v-data-table>
	</div>
</template>

<script>
	import { mapState } from 'vuex'
	import EditJurusan from '@/components/Jurusan/EditJurusan'
	import HapusJurusan from '@/components/Jurusan/HapusJurusan'
	import Loader from '@/components/Loader'

	export default{
		data: () => ({
			expanded: [],

			header: [
				{ text: 'No', value: 'no', sortable: false },
				{ text: 'Nama', value: 'nama', sortable: false },
				{ text: 'Jenjang', value: 'jenjang', sortable: false },
				{ text: 'Action', value: 'action', align: 'center', sortable: false },
        { text: '', value: 'data-table-expand' }
			],

			data: [
				{
					nama: 'IPA',
					jenjang: {
						id: 1,
						nama: 'SMA'
					},
					id: 1
				}
			],
		}),

		components: {
			Loader,
			HapusJurusan,
			EditJurusan
		},

		created() {
			this.$store.dispatch('dataJurusan/getDataJurusan')
			this.$store.dispatch('dataJenjang/getDataJenjang')
			this.$store.dispatch('dataJurusan/updateIsLoading', true)
		},

		computed: { 
			...mapState('dataJurusan', ['dataJurusan', 'isLoading'])
		}
	}
</script>